@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,900;1,100;1,400;1,500;1,700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  max-width: 100vw;
  font-family: "Roboto", arial, sans-serif;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
}

.component {
  display: grid;
  grid-template-columns: repeat(2, minmax(200px, 1fr));
  row-gap: 10px;
  column-gap: 95px;
  width: 1440px;
  height: auto;
  padding: 50px 0;
}
.component > .component-image {
  min-width: 400px;
  margin: 96px 0 0 230px;
}
.component > .component-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  margin: 136px 0 230px 95px;
}
.component > .component-content .component-header {
  font-size: 42px;
  font-weight: bold;
  line-height: 50.4px;
}
.component > .component-content .component-paragraph {
  width: 380px;
  height: auto;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  margin-top: 24px;
  color: #121212;
}
.component > .component-content .component-button {
  width: auto;
  height: 48px;
  line-height: 100%;
  margin-top: 24px;
  font-size: 16px;
  border: none;
  border-radius: 32px;
  padding: 16px 40px;
  font-weight: 700;
  background-color: #e4c1ae;
  color: #ffffff;
  cursor: pointer;
}
@media (max-width: 1040px) {
  .component {
    width: 80vw;
    min-width: 400px;
    grid-template-columns: 1fr;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .component > .component-image {
    margin: 0;
  }
  .component > .component-content {
    width: 100%;
    padding: 0 10px;
    text-align: center;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .component > .component-content > .component-paragraph {
    margin-top: 5px;
    width: auto;
    font-size: 21px;
    line-height: 32px;
  }
}
@media (max-width: 1040px) and (max-width: 633px) {
  .component > .component-content > .component-paragraph {
    font-size: 16px;
  }
}
@media (max-width: 1040px) and (max-width: 480px) {
  .component > .component-content > .component-paragraph {
    font-size: 13px;
  }
}
@media (max-width: 1040px) {
  .component > .component-content > .component-header {
    margin: 0 auto;
  }
}
@media (max-width: 1040px) {
  .component > .component-content > .component-button {
    width: 100%;
  }
}
@media (max-width: 1040px) and (max-width: 395px) {
  .component > .component-content > .component-button {
    margin-top: 10%;
  }
}
@media (max-width: 1040px) and (orientation: landscape) {
  .component {
    width: 100vw;
    min-width: 820px;
    grid-template-columns: repeat(2, minmax(200px, 1fr));
    column-gap: 10px;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: space-around;
  }
  .component > .component-image {
    margin: 0;
  }
  .component > .component-content > .component-header {
    height: 10%;
    font-size: 36px;
    margin: 0;
  }
  .component > .component-content > .component-paragraph {
    text-align: center;
    height: 80%;
    width: 50vw;
    font-size: 14px;
    line-height: 24px;
    margin-top: 24px;
    color: #121212;
  }
  .component > .component-content > .component-button {
    padding: 0;
    line-height: 100%;
    width: 80%;
  }
}

.modal-background {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: none;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal-background > .modal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  margin: 0 auto;
  height: 210px;
  width: 920px;
  border: none;
}
.modal-background > .modal > h2 {
  margin: 56px 0 0 95px;
  width: 380px;
  height: 50px;
  font-size: 42px;
  line-height: 50.4px;
  font-weight: 700;
  color: #121212;
}
.modal-background > .modal > p {
  width: 380px;
  height: 24px;
  margin: 24px 0 56px 95px;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
}
.modal-background > .modal > button.close-modal-button {
  position: absolute;
  top: 46px;
  right: 46px;
  width: 12px;
  height: 12px;
  font-weight: 700;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.modal-background > .modal > button.reset-user-counter-btn {
  position: absolute;
  bottom: 26px;
  right: 46px;
  width: auto;
  height: 48px;
  line-height: 100%;
  margin-top: 24px;
  font-size: 16px;
  border: none;
  border-radius: 32px;
  padding: 16px 40px;
  font-weight: 700;
  background-color: #e4c1ae;
  color: #ffffff;
  cursor: pointer;
}
@media (max-width: 940px) {
  .modal-background > .modal {
    width: 80vw;
  }
}
@media (max-width: 820px) {
  .modal-background > .modal {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80vh;
  }
  .modal-background > .modal > button.reset-user-counter-btn {
    position: static;
    margin-top: 20px;
  }
  .modal-background > .modal > * {
    text-align: center;
    margin: 10px !important;
  }
}
@media (max-width: 440px) {
  .modal-background > .modal {
    width: 100vw;
    font-size: 12px;
  }
  .modal-background > .modal > button.reset-user-counter-btn {
    position: static;
    margin-top: 20px;
  }
  .modal-background > .modal > * {
    text-align: center;
    margin: 10px !important;
  }
}