.modal-background {
  @include flex(column, center, center);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: none;
  background-color: rgba(0, 0, 0, 0.5);

  & > .modal {
    @include flex(column, center, start);
    margin: 0 auto;
    height: 210px;
    width: 920px;
    border: none;

    & > h2 {
      margin: 56px 0 0 95px;
      width: 380px;
      height: 50px;
      font-size: 42px;
      line-height: 50.4px;
      font-weight: 700;
      color: #121212;
    }

    & > p {
      width: 380px;
      height: 24px;
      margin: 24px 0 56px 95px;
      font-weight: 300;
      font-size: 16px;
      line-height: 24px;
    }

    & > button.close-modal-button {
      position: absolute;
      top: 46px;
      right: 46px;

      width: 12px;
      height: 12px;
      font-weight: 700;
      border: none;
      background-color: transparent;
      cursor: pointer;
    }

    & > button.reset-user-counter-btn {
      position: absolute;
      bottom: 26px;
      right: 46px;

      @include deafultButton;
    }

    @media (max-width: 940px) {
      & {
        width: 80vw;
      }
    }

    @media (max-width: 820px) {
      & {
        @include flex(column, center, center);
        height: 80vh;

        & > button.reset-user-counter-btn {
          position: static;
          margin-top: 20px;
        }
        & > * {
          text-align: center;
          margin: 10px !important;
        }
      }
    }

    @media (max-width: 440px) {
      & {
        width: 100vw;
        font-size: 12px;

        & > button.reset-user-counter-btn {
          position: static;
          margin-top: 20px;
        }
        & > * {
          text-align: center;
          margin: 10px !important;
        }
      }
    }
  }
}
